export class Champion {
    constructor(data) {
        this.version = data.version; // Versi√≥n del campe√≥n
        this.id = data.id; // ID del campe√≥n
        this.key = data.key; // Clave √∫nica del campe√≥n
        this.name = data.name; // Nombre del campe√≥n
        this.title = data.title; // T√≠tulo del campe√≥n
        this.blurb = data.blurb; // Descripci√≥n breve del campe√≥n
        this.info = {
            attack: data.info.attack, // Estad√≠stica de ataque
            defense: data.info.defense, // Estad√≠stica de defensa
            magic: data.info.magic, // Estad√≠stica de magia
            difficulty: data.info.difficulty // Dificultad de uso
        };
        this.tags = data.tags; // Etiquetas del campe√≥n
        this.partype = data.partype; // Tipo de recurso (por ejemplo, Man√°, Energ√≠a)
        this.stats = {
            hp: data.stats.hp, // Salud base
            mp: data.stats.mp, // Man√° base
        };

        this.image = null; 
    }

    setImage(sprite) {
       
        this.image = sprite
        // console.log(this.image)
    }

    getDescription() {
        return `${this.name}, ${this.title}: ${this.blurb}`;
    }

    getStats() {
        return {
            hp: this.stats.hp,
            mp: this.stats.mp,
            attack: this.info.attack,
            defense: this.info.defense,
            magic: this.info.magic,
            difficulty: this.info.difficulty
        };
    }
}
